@Injectable()
export class IconManager {
  private _tmplIcons: KeyValueObject<Icon> = {
    'tmplCloud': {
      icon: Icons.CLOUD,
      color: '#D9D9D9'
    },
    'tmplCloudForbidden': {
      icon: Icons.CLOUD_FORBIDDEN,
      color: '#FA7A59'
    },
    'tmplArrow': {
      icon: Icons.ARROW,
      color: '#FBFBFB'
    },
    'tmplDirectory': {
      icon: Icons.DIRECTORY
    },
    'tmplSearch': {
      icon: Icons.SEARCH,
      color: '#666666'
    }
  };

  private _icons: KeyValueObject<Icon> = {
    'doc': {
      icon: Icons.WORD,
      color: '#5682B5'
    },
    'docx': {
      icon: Icons.WORD,
      color: '#5682B5'
    },
    'jpg': {
      icon: Icons.IMG,
      color: '#BF0AA6'
    },
    'png': {
      icon: Icons.IMG,
      color: '#BF0AA6'
    },
    'gif': {
      icon: Icons.IMG,
      color: '#BF0AA6'
    },
    'jpeg': {
      icon: Icons.IMG,
      color: '#BF0AA6'
    },
    'bmp': {
      icon: Icons.IMG,
      color: '#BF0AA6'
    },
    'xls': {
      icon: Icons.XLS,
      color: '#1BA36E'
    },
    'xlsx': {
      icon: Icons.XLS,
      color: '#1BA36E'
    },
    'ppt': {
      icon: Icons.PPT,
      color: '#D2390E'
    },
    'pptx': {
      icon: Icons.PPT,
      color: '#D2390E'
    },
    'zip': {
      icon: Icons.ARCHIVE,
      color: '#474A56'
    },
    '7z': {
      icon: Icons.ARCHIVE,
      color: '#474A56'
    },
    'dmg': {
      icon: Icons.ARCHIVE,
      color: '#474A56'
    },
    'rar': {
      icon: Icons.ARCHIVE,
      color: '#474A56'
    },
    'pdf': {
      icon: Icons.PDF,
      color: '#990719'
    },
    'avi': {
      icon: Icons.MOVIE,
      color: '#48CFAD'
    },
    'defaultIcon': {
      icon: Icons.DEFAULT,
      color: '#D4D4D4'
    }
  };

  constructor(private _sanitizer: DomSanitizer) {
  }

  /**
   * Initiates the icons set
   */
  init(icons: KeyValueObject<Icon>): void {
    Object.assign(this._icons, icons, this._tmplIcons);
  }

  /**
   * Gets the icon for file type and sets up the base color
   */
  getIcon(type: string, baseColor: string = '#000000'): string {
    let typeIcon = this._icons[type];

    typeIcon = typeIcon || this._icons.defaultIcon;

    return typeIcon.icon.replace('{baseColor}', typeIcon.color || baseColor);
  }

  getTmplComponentIcons(): KeyValueObject<string> {
    return Object.keys(this._tmplIcons).reduce((acc, current) => {
      acc[current] = this._sanitizer.bypassSecurityTrustHtml(this.getIcon(current));

      return acc;
    }, {});
  }
}
